.wrapper {
  background: rgb(0, 0, 0);
  box-shadow: 
    /* outer white */ -5px 0 40px #f0f,
    /* outer left magenta */ 5px 0 40px #0ff; /* outer right cyan */

  padding: 3em 2em;
  border-radius: 30px;
  min-height: 600px;
  min-width: 300px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

h1 {
  margin-bottom: 1em;
  text-align: center;
  font-size: 2em;
  color: rgb(253, 154, 253);
}

.input {
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
  background-color: rgb(252, 210, 252);
  padding: 10px;
  border-radius: 10px;
}

input {
  font-family: "Handlee", cursive;
  width: 350px;
  border-radius: 10px;
  border: none;
  font-size: 1.5em;
  background-color: transparent;
}
input:focus {
  outline: none;
}

button {
  background-color: transparent;
  padding: 0.5em 1.5em;
  border-radius: 0 0.5em 0.5em 0;
  border: 1px solid transparent;
  font-size: 16px;
  font-family: "Handlee", cursive;
  font-weight: bold;
  cursor: pointer;
  color: black;
  text-transform: uppercase;
}

button:hover {
  color: rgb(255, 0, 255);
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  font-size: 1.5em;
  cursor: default;
  font-style: italic;
  color: rgb(252, 210, 252);
}

.crossed-line {
  text-decoration: line-through;
  color: rgb(253, 154, 253);
  opacity: 0.5;
}
